interface User {
  id: string;
  name: string;
  email: string;
  role: string;
  status: 'active' | 'inactive' | 'pending';
  avatar: string;
  alt: string;
  lastActive: string;
  department: string;
}

interface UserManagementCardProps {
  users: User[];
}

